import React from 'react';
import { Col, ProgressBar, Row } from 'react-bootstrap';

const ProfileHeader = () => {
  
  return (
    <Row className='profileHeader px-5 d-flex align-items-center justify-content-between'>
      <Col xl={3} className='d-flex'>
        <h2 className='fw-bold fs-3 mb-0'>Complete Profile</h2>
      </Col>
      <Col xl={4} className='d-flex align-items-center justify-content-between'>
        <h6 className='fw-semibold text-nowrap mb-0'>Profile Completed</h6>
        <div className='w-75 mx-3'>
          <ProgressBar now={20} />
        </div>
        <h6 className='fw-semibold mb-0'>20%</h6>
      </Col>
    </Row>
  )
}

export default ProfileHeader