import React, { useState } from "react";
import { Accordion, Button, Form } from "react-bootstrap";
import { AiOutlineLeft, AiOutlineRight } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import ProfileLayout from "../../../components/ProfileLayout/CompleteProfile";
import { PATH } from "../../../utils/path";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import {
  createPatientHistory,
  deletePatientHistory,
  getPatientHistory,
  updatePatientHistory,
} from "../../../app/features/patientHistory/patientHistory.slice";
import { useEffect } from "react";
import { BsPencilSquare } from "react-icons/bs";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { ConfirmationModal } from "../../../components/Modal/ConfirmationModal";

const MedicalHistory = () => {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { patientHistoryData } = useSelector((state) => state.patientHistory);
  const [historyType, setHistoryType] = useState("");
  const [medicalHistory, setmedicalHistory] = useState("");
  const [deleteModel, setDeleteModel] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const [editing, setEditing] = useState(false);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  }, [dispatch, user?.userId]);

  const moveToNextMedicalHistory = () => {
    setmedicalHistory("");
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
    setEditing(false);
  };

  function onSubmit(data) {
    const finalData = {
      patientId: user?.userId,
      historyTypeId:
        historyType === 1
          ? 1
          : historyType === 2
          ? 2
          : historyType === 3
          ? 3
          : null,
      pastMedicalHistory: data?.pastMedicalHistory,
      currentMedical: data?.currentMedical,
      allergyHistory: data?.allergyHistory,
    };
    if (patientHistoryData?.historyTypeId) {
      dispatch(updatePatientHistory({ finalData, moveToNextMedicalHistory }));
    } else {
      dispatch(createPatientHistory({ finalData, moveToNextMedicalHistory }));
    }
  }
  /** Delete Medical */
  const moveToNextDeleteMedicalHistory = () => {
    setmedicalHistory("");
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientHistory(finalData));
  };
  const onDelete = (data) => {
    const finalData = {
      patientId: user?.userId,
      historyTypeId: deleteId ? deleteId : null,
    };
    dispatch(
      deletePatientHistory({ finalData, moveToNextDeleteMedicalHistory })
    );
  };
  return (
    <div>
      <ProfileLayout className="pt-5">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <div className="mx-auto width_75 medical_history">
            <div className="width_75 mx-auto">
              {/* {MEDICAL_HISTORY_LIST.map((data, index) => { */}
              {/* return ( */}
              <Accordion className="mb-3" defaultActiveKey="0">
                <Accordion.Item
                  className="border-0 mb-3 accordion_item"
                  eventKey="0"
                >
                  <Accordion.Header className="accordion_header fw-semibold">
                    Past Medical
                  </Accordion.Header>
                  <Accordion.Body className="">
                    {patientHistoryData?.pastMedicalHistory &&
                    editing === false ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.pastMedicalHistory}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(1);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="pastMedicalHistory"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.pastMedicalHistory ?? ""
                              }
                              rows={12}
                              {...register("pastMedicalHistory")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(1)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item
                  className="border-0 mb-3 accordion_item"
                  eventKey="1"
                >
                  <Accordion.Header className="accordion_header fw-semibold">
                    Current Medical
                  </Accordion.Header>
                  <Accordion.Body>
                    {patientHistoryData?.currentMedical && editing === false ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.currentMedical}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(2);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="currentMedical"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.currentMedical ?? ""
                              }
                              rows={12}
                              {...register("currentMedical")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(2)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item
                  className="border-0 mb-3 accordion_item"
                  eventKey="2"
                >
                  <Accordion.Header className="accordion_header fw-semibold">
                    Allergy History (With any Medication)
                  </Accordion.Header>
                  <Accordion.Body className="">
                    {(patientHistoryData?.allergyHistory && editing === false) ? (
                      <div className="d-flex justify-content-between py-3">
                        <div className="pe-2">
                          <p
                            style={{
                              textAlign: "justify",
                              marginRight: "10px",
                            }}
                          >
                            {patientHistoryData?.allergyHistory}
                          </p>
                        </div>
                        <div>
                          <div
                            className="d-flex justify-content-center align-items-center mb-2"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#E1EBFF",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => setEditing(true)}
                          >
                            <BsPencilSquare
                              style={{
                                color: "#2269F2",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                          <div
                            className="d-flex justify-content-center align-items-center"
                            style={{
                              height: "29px",
                              width: "32px",
                              backgroundColor: "#FFDADD",
                              borderRadius: "5px",
                              cursor: "pointer",
                            }}
                            onClick={() => {
                              setDeleteModel(true);
                              setDeleteId(3);
                            }}
                          >
                            <RiDeleteBin5Fill
                              style={{
                                color: "#E63946",
                                fontSize: "18px",
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <>
                        <div className="accordion_content p-3 position-relative">
                          <Form.Group className="mb-3">
                            <Form.Control
                              name="allergyHistory"
                              as="textarea"
                              defaultValue={
                                patientHistoryData?.allergyHistory ?? ""
                              }
                              rows={12}
                              {...register("allergyHistory")}
                            />
                          </Form.Group>
                        </div>
                        <div className="mb-2 d-flex justify-content-end pe-3">
                          <Button
                            onClick={() => setmedicalHistory("")}
                            className="px-2 py-2 mt-2"
                            size="sm"
                            style={{
                              background: "#eae5e5",
                              borderColor: "#eae5e5",
                              color: "#000",
                              marginRight: "10px",
                            }}
                          >
                            Cancel
                          </Button>
                          <Button
                            onClick={() => setHistoryType(3)}
                            className="px-4 py-2 mt-2 primary_bg"
                            size="sm"
                            type="submit"
                          >
                            Save
                          </Button>
                        </div>
                      </>
                    )}
                    {/* <div className="accordion_content p-3 position-relative">
                      <Form.Group className="mb-3">
                        <Form.Control
                          name="allergyHistory"
                          as="textarea"
                          defaultValue={patientHistoryData?.allergyHistory}
                          rows={12}
                          {...register("allergyHistory")}
                        />
                      </Form.Group>
                    </div>
                    <div className="mb-2 d-flex justify-content-end pe-3">
                      <Button
                        onClick={() => setHistoryType(3)}
                        className="px-4 py-2 mt-2 primary_bg"
                        size="sm"
                        type="submit"
                      >
                        Save{" "}
                      </Button>
                    </div> */}
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            </div>
            <div className="d-flex justify-content-end width_75 mx-auto">
              <Button
                onClick={() => navigate(PATH.PERSONAL_INFORMATION)}
                className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 me-2 primary_outline"
                size="sm"
              >
                {" "}
                <AiOutlineLeft className="fs-6 me-1" />
                Back
              </Button>
              <Button
                onClick={() => navigate(PATH.PHARMACY)}
                className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 primary_bg"
                size="sm"
                variant="primary"
              >
                Next <AiOutlineRight className="fs-6 ms-1" />
              </Button>
            </div>
          </div>
        </Form>
      </ProfileLayout>
      {deleteId === 1 ? (
        <ConfirmationModal
          show={deleteModel}
          onHide={() => setDeleteModel(false)}
          heading={"Delete Past Medical History"}
          title={"this Past Medical History"}
          removeFunc={onDelete}
        />
      ) : deleteId === 2 ? (
        <ConfirmationModal
          show={deleteModel}
          onHide={() => setDeleteModel(false)}
          heading={"Delete Current Medical History"}
          title={"this Current Medical History"}
          removeFunc={onDelete}
        />
      ) : (
        <ConfirmationModal
          show={deleteModel}
          onHide={() => setDeleteModel(false)}
          heading={"Delete Allergy History"}
          title={"this Allergy History"}
          removeFunc={onDelete}
        />
      )}
    </div>
  );
};

export default MedicalHistory;
